#root {
  padding-top: 30px;

}

@media screen and (max-width: 667px) {
  #root {
    padding-top: 0;
  }
}

.container {
  position: relative;

  max-width : 1200px;
  margin    : 0 auto;
  padding   : 20px;
  box-sizing: border-box;
  background: #f1f1f1;
}

.goods-field {
  display        : flex;
  justify-content: space-between;
}

@media screen and (max-width: 567px) {
  .goods-field {
    flex-wrap: wrap;
  }
}

.goods-field::after {
  content: "";
  display: block;
  clear  : both;
}

.goods-block {
  width     : 30%;
  margin    : .7%;
  padding   : 10px;
  border    : 1px solid black;
  float     : left;
  text-align: center;
}

@media (max-width: 567px) {
  .goods-block {
    width: 100%;
  }

}

.goods-block img {
  max-width: 80%;
  display  : block;
  margin   : 20px auto;
}

.cart,
.return-btn {
  align-items    : center;
  border         : 1px solid #c7c7c7;
  border-radius  : 5px;
  cursor         : pointer;
  display        : flex;
  justify-content: center;
  height         : 50px;
  position       : absolute;
  top            : 15px;
  right          : 30px;
  width          : 50px;
  box-shadow     : 0 0 4px 0 #ccc;
}

.cart:active,
.return-btn:active {
  box-shadow: none;
}

.cart-count {
  background   : darkgray;
  position     : absolute;
  content      : '';
  height       : 30px;
  width        : 30px;
  top          : 50%;
  right        : -25px;
  border-radius: 50%;
  transform    : translateY(-50%);
  text-align   : center;
  color        : #fff;
  font-size    : 20px;
  font-weight  : 700;

}

.return-btn {
  right : 100px;
  height: 52px;
  width : initial;
}


table {
  margin        : 0 auto;
  box-shadow    : 0 0 4px 0 darkgray;
  width         : 60%;
  border-spacing: 0;
  text-align    : center;
  border-radius : 5px;
  overflow      : hidden;
}

@media screen and (max-width: 768px) {
  table {
    width: 100%;
  }
}


.td-wrap {
  display    : flex;
  align-items: center;
}

.td-wrap>div {
  margin-left: 10px;
}


th,
td {
  width         : 16%;
  border-bottom : 1px solid #ccc;
  padding       : 10px 0;
  vertical-align: middle;
}

tr:last-child th {
  border-right : 1px solid #ccc;
  text-align   : right;
  padding-right: 10px;
}

tr:last-child td {
  text-align  : left;
  padding-left: 40px;
}

td:first-child {
  text-align  : left;
  padding-left: 10px;
}

td:not(:last-child) {
  border-right: 1px solid #ccc;
}

.trash-can img {
  border       : 1px solid #ccc;
  border-radius: 5px;
  box-shadow   : 0 0 4px 0 #ccc;
}

.trash-can>img:active {
  box-shadow: none;
}


/* media */

@media (max-width: 567px) {
  .container {
    padding: 10px;
  }

  .goods-block img {
    margin: 0 auto;
  }

  th {
    font-size: 12px;
  }
  td{
    font-size: 14px;
  }
  .return-btn {
    font-size: 12px;
    right: 85px;
  }
  .td-wrap>div{
    font-size: 14px;
    margin-left: 3px;
  }
  .td-wrap>img{
    width: 20px;
  }
  .trash{
    width: 25px;
  }
}